<template>
  <!-- v-loading.fullscreen.lock="fullscreenLoading" eui载入菊花 -->
  <div id="login" v-loading.fullscreen.lock="fullscreenLoading" class="clearfix">
    <!-- 头部title -->
    <header>
      <h1>OA</h1>
      <hgroup>
        <h2>办公管理</h2>
        <h2>自动化系统</h2>
      </hgroup>
    </header>
    <!-- 登录&找回密码框 -->
    <div class="login">
      <!-- eui tab切换 -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="登录" name="first"></el-tab-pane>
        <el-tab-pane label="找回密码" name="second"></el-tab-pane>
      </el-tabs>
      <!-- vue css 过渡,增加用户体验 -->
      <transition name="fade1">
        <!-- 登录表单提交 -->
        <!-- model 绑定数据; rules 制定规则; ref 传递表单 to submitForm('loginForm') -->
        <el-form :model="loginForm" :rules="rules" ref="loginForm" class="el-form firstform" v-show="loginFormShow">
          <!-- prop 组件通信 指定传过去验证提交 -->
          <el-form-item label="工号" prop="workid" class="pinput puser">
            <el-input type="text" v-model="loginForm.workid" auto-complete="off" placeholder="请正确输入工号"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password" class="pinput">
            <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="请正确输入密码"></el-input>
          </el-form-item>
          <el-form-item>
            <p class="pextra">
              <span class="lspan"><input type="checkbox" name="remember" id="remember" v-model="remember">
                <label for="remember">记住密码</label>
              </span>
              <span class="rspan">
                <a @click="forgetClick">忘记密码</a>
              </span>
            </p>
          </el-form-item>
          <el-form-item class="pbutton">
            <el-button type="primary" @click="submitForm('loginForm')">登
              <span class="extraspan"></span>录</el-button>
          </el-form-item>
        </el-form>
      </transition>
      <!-- vue css 过渡,增加用户体验 -->
      <transition name="fade2">
        <el-form :model="findpwForm" :rules="rules" ref="findpwForm" class="el-form form2" v-show="findFormShow">
          <el-form-item label="工号" prop="workid" class="findworkid rmove">
            <el-input type="text" v-model="findpwForm.workid" auto-complete="off" placeholder="请正确输入工号"></el-input>
          </el-form-item>
          <el-form-item label="问题一" class="question">
            <div class="sub-title">你的至亲名字?</div>
          </el-form-item>
          <el-form-item label="答案" prop="answer1" class="question rmove">
            <el-input type="text" v-model="findpwForm.answer1" auto-complete="off" placeholder="请正确输入答案"></el-input>
          </el-form-item>
          <el-form-item label="问题二" class="question">
            <div class="sub-title">你的至亲联系方式</div>
          </el-form-item>
          <el-form-item label="答案" prop="answer2" class="question rmove">
            <el-input type="text" v-model="findpwForm.answer2" auto-complete="off" placeholder="请正确输入答案"></el-input>
          </el-form-item>
          <el-form-item class="pbutton psubmit">
            <el-button type="primary" @click="submitFind('findpwForm')">提
              <span class="extraspan"></span>交</el-button>
          </el-form-item>
        </el-form>
      </transition>
    </div>
    <!-- 尾部 说明版权 -->
    <footer>
      <p>Copyright &copy; 2017 OA Power By Team9.</p>
    </footer>

    <!-- 提交问题成功,弹出重置密码 -->
    <div class="reset">
      <!-- eui :visible.sync="dialogFormVisible" 绑定呈现 -->
      <el-dialog title="重置密码" :visible.sync="dialogFormVisible">
        <el-form :model="resetpwform" :rules="rules" ref="resetpwform">
          <el-form-item label="重设密码" :label-width="formLabelWidth" prop="password">
            <el-input v-model="resetpwform.password" auto-complete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" :label-width="formLabelWidth" prop="checkpw">
            <el-input v-model="resetpwform.checkpw" auto-complete="off" type="password"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitReset('resetpwform')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    
    <router-view></router-view>
  </div>
</template>

<script>
// 引入路由
import router from '../main'
import {setCookie,getCookie,delCookie} from '../cookies.js'
export default {
  name: 'login',
  data: function() {
    // 验证用户名
    var validateUser = (rule, value, callback) => {
      var uPattern = /^[0-9]{6,10}$/; //账号正则:4到10位数字
      if (value === '') {
        callback(new Error('请输入工号'));
      } else {
        if (!uPattern.test(value)) {
          console.log(uPattern.test(value));
          callback(new Error('工号格式不对'));
        } else {
          this.workid = value;
          callback();
        }
      }
    };
    // 验证密码
    var validatePass = (rule, value, callback) => {
      // alert("1111");
      var pPattern = /^(?![a-zA-Z]+$)(?![A-Z0-9]+$)(?![A-Z\W_]+$)(?![a-z0-9]+$)(?![a-z\W_]+$)(?![0-9\W_]+$)[a-zA-Z0-9]{8,20}$/; //密码正则:a-z|A-Z|0-9
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (!pPattern.test(value)) {
          // console.log(pPattern.test(value));
          callback(new Error('密码格式不对'));
        } else {
          this.password = value;
          callback();
        }
      }
    };
    // 重置密码校验
    var validatePass2 = (rule, value, callback) => {
      var pPattern = /^(?![a-zA-Z]+$)(?![A-Z0-9]+$)(?![A-Z\W_]+$)(?![a-z0-9]+$)(?![a-z\W_]+$)(?![0-9\W_]+$)[a-zA-Z0-9]{8,20}$/; //密码正则:a-z|A-Z|0-9
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.resetpwform.password) {
        callback(new Error('两次输入密码不一致!'));
      } else if (!pPattern.test(value)) {
        callback(new Error('密码格式不对'));
      } else {
        this.password = value;
        callback();
      }
    };
    // 验证答案1
    var validateAns1 = (rule, value, callback) => {
      var aPattern = /^[\u4E00-\u9FA5A-Za-z0-9_]{1,50}$/;
      if (value === '') {
        callback(new Error('请输入答案'));
      } else {
        if (!aPattern.test(value)) {
          console.log(aPattern.test(value));
          callback(new Error('答案格式不对'));
        } else {
          this.answer1 = value;
          callback();
        }
      }
    };
    // 验证答案2
    var validateAns2 = (rule, value, callback) => {
      var aPattern = /^[0-9]{1,20}$/;
      if (value === '') {
        callback(new Error('请输入答案'));
      } else {
        if (!aPattern.test(value)) {
          console.log(aPattern.test(value));
          callback(new Error('答案格式不对'));
        } else {
          this.answer2 = value;
          callback();
        }
      }
    };
    // 数据初始化
    return {
      loginFormShow: true,
      findFormShow: false,
      fullscreenLoading: false,
      loginForm: {
        workid: '',
        password: ''
      },
      findpwForm: {
        workid: '',
        answer1: '',
        answer2: ''
      },
      resetpwform: {
        password: '',
        checkpw: ''
      },
      // 表单规则制定,制定校验和事件
      rules: {
        workid: [
          { validator: validateUser, trigger: 'blur' }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkpw: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        answer1: [
          { validator: validateAns1, trigger: 'blur' }
        ],
        answer2: [
          { validator: validateAns2, trigger: 'blur' }
        ]
      },
      activeName: 'first',
      remember: false,
      dialogFormVisible: false,
      form: {
        name: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '80px'
    };
  },
  methods: {
    submitForm(formName) {
      // 注意ref和validate
      this.$refs[formName].validate((valid) => {
        if (valid) {

          // start-loginreq
          this.$http({
            url: '/api/login',
            method: 'POST',
            emulateJSON: true,
            body: {
              username: this.workid,
              password: this.password
              // ,
              // rememberMe: this.remember
            },
            headers: {
              // "content-type": "application/json"
              "content-type": "application/x-www-form-urlencoded"
            },
            timeout: 2000,
            before: function(request) {
              // console.log("request in before");
              // console.log(request);
              // console.log(typeof(request.body));
              this.fullscreenLoading = true;
              // console.log("this.fullscreenLoading:"+this.fullscreenLoading);
            }
          }).then(
            function(res) {
              console.log(res);
              console.log("success");
              if (res.body.status === 1) {
                // console.log(res.body.msg)
                this.fullscreenLoading = false;
                // console.log(res.body.status);
                
                //登录成功,记住密码,设置cookies
                // start-rem
                if(this.remember == true){
                  setCookie("id",this.workid);
                  setCookie("rem",this.remember);
                  setCookie("pw",this.password);
                }else{
                  delCookie("id");
                  delCookie("rem");
                  delCookie("pw");
                }
                // end-rem

                // router跳转
                // console.log("router跳转+"+this.workid);
                // sessionStorage.setItem('access-user', JSON.stringify({userId:this.workid,rememberMe:this.remember}));
                localStorage.setItem('access-user', JSON.stringify({userId:this.workid}));
                // setCookie("id",this.workid);
                // setCookie("rem",this.remember);
                this.$router.push({name: 'index',params:{userId: this.workid}});
              } else if (res.body.status === 0) {
                this.fullscreenLoading = false;
                this.$alert(res.body.msg, "登录失败", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
              } else {
                this.fullscreenLoading = false;
                this.$alert("请联系管理员", "无法识别", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
              }
            },
            function(res) {
              // console.log("failed");
              this.fullscreenLoading = false;
              this.$alert("请检查网络", "请求超时", {
                confirmButtonText: '确定',
                callback: action => { }
              });

            }
            );
          // end-loginreq

        } else {
          this.$alert("表单填写有误", "请求超时", {
            confirmButtonText: '确定',
            callback: action => { }
          });
          return false;
        }
      });
    },
    submitFind(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.workid);
          // console.log(this.answer1)
          // console.log(this.answer2)
          this.$http({
            url: '/api/findpw',
            method: 'POST',
            emulateJSON: true,
            body: {
              userId: this.workid,
              answer1: this.answer1,
              answer2: this.answer2
            },
            headers: {
              // "content-type": "application/json"   //设置成json,需要修改emulateJSON为true
              "content-type": "application/x-www-form-urlencoded"
            },
            timeout: 2000,
            before: function(request) {
              // console.log("request in before");
              this.fullscreenLoading = true;
            }
          }).then(
            function(res) {
              // console.log(res);
              // console.log("success");
              if (res.body.status === 1) {
                // console.log(res.body.msg)
                this.fullscreenLoading = false;
                this.dialogFormVisible = true
              } else if (res.body.status === 0) {
                this.fullscreenLoading = false;
                this.$alert(res.body.msg, "找回密码失败", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
              } else {
                this.$alert("请联系管理员", "无法识别", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
              }
            },
            function(res) {
              console.log("failed");
              this.fullscreenLoading = false;
              this.$alert("请检查网络", "请求超时", {
                confirmButtonText: '确定',
                callback: action => { }
              });

            }
            );
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    submitReset(formName) {

      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.workid);
          console.log(this.password);
          this.$http({
            url: '/api/resetpw',
            method: 'POST',
            emulateJSON: true,
            body: {
              userId: this.workid,
              newpassword: this.password
            },
            headers: {
              // "content-type": "application/json"   //设置成json,需要修改emulateJSON为true
              "content-type": "application/x-www-form-urlencoded"
            },
            timeout: 2000,
            before: function(request) {
              // console.log("request in before");
              this.fullscreenLoading = true;
            }
          }).then(
            function(res) {
              console.log(res);
              console.log("success");
              if (res.body.status === 1) {
                console.log(res.body.msg)
                this.fullscreenLoading = false;
                this.dialogFormVisible = false;
                this.$alert(res.body.msg, "重置密码成功", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
                this.activeName = 'first';
                this.loginFormShow = true;
                this.findFormShow = false;
              } else if (res.body.status === 0) {
                this.fullscreenLoading = false;
                this.$alert(res.body.msg, "重置密码失败", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
              } else {
                this.$alert("请联系管理员", "无法识别", {
                  confirmButtonText: '确定',
                  callback: action => { }
                });
              }
            },
            function(res) {
              console.log("failed");
              this.fullscreenLoading = false;
              this.$alert("请检查网络", "请求超时", {
                confirmButtonText: '确定',
                callback: action => { }
              });

            }
            );
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    handleClick(tab, event) {
      if (tab.name == "first") {

        this.loginFormShow = true;
        this.findFormShow = false;

      }
      if (tab.name == "second") {
        this.findFormShow = true;
        this.loginFormShow = false;

      }
    },
    forgetClick() {
      this.activeName = "second";
      this.loginFormShow = false;
      this.findFormShow = true;
    }
  },
  mounted: function () {
      if(getCookie("rem")=="true"){
        this.remember = getCookie("rem");
        this.loginForm.workid = getCookie("id");
        this.loginForm.password = getCookie("pw");

      }
  }
}
</script>

<style>

#login header h1 {
    margin-top: 66px;
    margin-left: 70px;
    float:left;
    font-size: 56px;
    font-weight: 500;
}

#login header hgroup {
    margin-top: 82px;
    margin-left: -28px;
    float:left;
    text-align: right;
}
#login header hgroup h2 {    
    font-weight: 500;
    font-size: 35px;
}

.login {
    background-color: rgba(0, 0, 0, 0.4);
    color: #138aff;
    position: fixed;
    box-sizing: border-box;
    width: 330px;
    height: 310px;
    top: 50%;
    right: 0;
    border: 1px solid #999;
    transform: translate(-30%, -50%);
    padding-top:4px;
    padding-left:25px;
    padding-right:25px;
    font-size: 14px;
    border-radius: 4px;
}

.el-form {
    font-size: 14px;
}

.plogin {
    box-sizing: border-box;
    width:100%;
    height:52px;
    line-height: 52px;
    letter-spacing: 8px;
    padding-left:14px;
}

.pinput {
    box-sizing: border-box;
    width:100%;
    height:58px;
    line-height: 58px;
    padding-left:14px;
    display: inline-block;
}

.pinput input,.question input,.findworkid input {
    outline-color:#138aff;
    height:28px;
    width: 196px;
    background-color: transparent;
    padding-left: 6px;
    margin-right: 3px;
    color:#138aff;
    font-size: 14px;
}

.puser {
    margin-top:14px;
}

.pextra {
    box-sizing: border-box;
    padding: 0 14px;
    height:50px;
    width: 100%;
    line-height: 50px;
    color:#FF4D39;
}
.lspan {
    float: left;
}
.lspan input[type=checkbox] {
    vertical-align: middle;
    margin-right: 3px;
}
.lspan label:hover,.rspan a:hover {
    color:#FF0000;
}
.rspan {
    float: right;
}
.rspan a {
    color:#FF4D39;
}
.pbutton button{
    height: 40px;
    width: 250px;
    margin: 0 14px;
    background-color: #169bd5;
    outline: none;
    border: none;
    color: #fff;
}
.pbutton button:hover{
    background-color: rgba(22, 155, 213, 0.7);
}
.pbutton button .extraspan {
    display:inline-block;
    width:58px;
}

#login footer p {
    width: 100%;
    position: fixed;
    bottom: 20px;
    text-align: center;
    margin: 0 auto;
    color: #C9C9C9;
    font-size: 12px;
}

.usernull,
.pwnull {
  color: #FF4D39;
}

.login .el-form-item__label {
  color: #138aff;
  /* line-height: 58px; */
  float: none;
}

.login .el-form-item__content {
  display: inherit;
}

.login .el-form-item {
  margin-bottom: 0px;
}

.login .el-tabs__header {
  margin: 0;
}

.login .el-tabs__item.is-active {

  color: #138aff;
}

.login .el-tabs__active-bar {
  background-color: #138aff;
}

.question {
  box-sizing: border-box;
  width: 100%;
  height: 36px;
  line-height: 36px;
  padding-left: 14px;
  display: inline-block;
}

.findworkid {
  margin-top: 10px;
  box-sizing: border-box;
  width: 100%;
  height: 36px;
  line-height: 36px;
  padding-left: 14px;
  display: inline-block;
}

.sub-title {
  color: #999;
}

.rmove {
  margin-left: 8px;
}

.psubmit {
  margin-top: 16px;
}

.el-form-item__error {
  width: 74px;
  text-align: right;
  padding-top: 0px;
  left: 120px;
  right: 0;
}

.firstform {
  margin-top: 8px;
}

.fade1-enter-active {
  transition: all .3s ease;
}

.fade1-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.fade1-enter,
.fade1-leave {
  transform: translateX(15px);
  opacity: 0;
}

.fade2-enter-active {
  transition: all .3s ease;
}

.fade2-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.fade2-enter,
.fade2-leave {
  transform: translateX(-15px);
  opacity: 0;
}

.form2 {
  position: absolute;
  top: 48px;
}

.el-loading-mask {
  background-color: transparent;
}

.reset .el-form .el-form-item:last-child {
  margin-bottom: 10px;
}

.reset .el-dialog__body {
  padding: 30px 20px 0 20px;
}

.reset .el-dialog--small {
  width: 30%;
}

.reset .el-form-item__error {
  width: 166px;
  margin-top: 2px;
}
</style>



